@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
#header {
    width: 100%;
    height: 80vh;
    margin-top: 20px;
}

#header h1{
    margin: 0px 0px 10px 0px;
    font-size: 41px;
    font-weight: 700;
    color: black;
}

#header .brand-name{
    color: #e75b5b;
    font-size: 48px;
}

#header h2{
    font-size: 21px;
    font-weight: 100;
    color: #2b97c9;
}

#header .btn-get-started{
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px 30px;
    border-radius: 50px;
    color: #2b97c9;
    transition: 0.5s;
    border: 2px solid #2b97c9;
    text-decoration: none;
}

#header .btn-get-started:hover{
    background-color:#2b97c9;
    color: #fff;
}

.header-img{
    text-align: right;
}

#header .animated{
    animation: up-down 1.5s ease-in-out infinite alternate-reverse both;
}

@keyframes up-down{
    0%{
        transform: translateY(15px);
    }
    100%{
        transform: translateY(-15px);
    }
}

@media screen and (max-width:992px) {
    #header .buut{
        margin-left: auto;
        margin-right: auto;
    }
    #header h1 h2{
        margin-left: auto;
        margin-right: auto;
        text-align: center;
    }
}
.navbar-brand{
    font-size: 1.9rem !important;
    color: #e75b5b !important;
  }

  .nav-item a{
    font-size: 1.2rem !important;
    text-transform: capitalize !important;
    text-decoration: none !important;
  }

  .nav-item a:hover{
    color: #e75b5b !important;
    font-size: 1.2rem !important;
   
    text-decoration: none !important;
  }

  
  .navbar-nav{
    margin-left: auto !important;
  }

.menu-active{
    font-weight: bold;
    color: #2b97c9 !important;
    border-bottom: 1px solid #e75b5b;
}


@media (max-width:991px) {
    .navbar-brand{
        font-size: 1.5.rem !important;
    }
    .menu-active{
        font-weight: bold;
        border-bottom: none;
    }
}
*{
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
  font-family: 'Mulish', sans-serif;
}



